<template>
  <div>
    <fTitle>
      <template #title>TimePicker 时间选择器</template>
      <template #introduce>用于选择或输入时间</template>
    </fTitle>
    <sTitle>
      <template #title>固定时间点</template>
      <template #introduce>提供几个固定的时间点供用户选择</template>
    </sTitle>
    <div class="outbox">
      <div class="inbox">
        <el-row type="flex" justify="space-around">
          <el-col :span="10"> <div class="grid-content">
            <el-time-select
  v-model="value1"
  :picker-options="{
    start: '08:30',
    step: '00:15',
    end: '18:30'
  }"
  placeholder="选择时间">
</el-time-select>
          </div></el-col>
        </el-row>
      </div></div>
      <sTitle>
      <template #title>任意时间点</template>
      <template #introduce>可以选择任意时间,两种选择方式</template>
    </sTitle>
    <div class="outbox">
      <div class="inbox">
        <el-row type="flex" justify="space-around">
          <el-col :span="10"> <div class="grid-content">
            <el-time-picker
    v-model="value2"
    :picker-options="{
      selectableRange: '18:30:00 - 20:30:00'
    }"
    placeholder="任意时间点">
  </el-time-picker>
          </div></el-col>
          <el-col :span="10"> <div class="grid-content">
            <el-time-picker
    arrow-control
    v-model="value3"
    :picker-options="{
      selectableRange: '18:30:00 - 20:30:00'
    }"
    placeholder="任意时间点">
  </el-time-picker>
          </div></el-col>
        </el-row>
      </div></div>

      <sTitle>
      <template #title>固定时间范围</template>
      <template #introduce>若先选择开始时间，则结束时间内备选项的状态会随之改变</template>
    </sTitle>
    <div class="outbox">
      <div class="inbox">
        <el-row type="flex" justify="space-around">
          <el-col :span="10"> <div class="grid-content">
    <el-time-select
    placeholder="起始时间"
    v-model="startTime"
    :picker-options="{
      start: '08:30',
      step: '00:15',
      end: '18:30'
    }">
  </el-time-select>
          </div></el-col>
          <el-col :span="10"> <div class="grid-content">
    <el-time-select
    placeholder="结束时间"
    v-model="endTime"
    :picker-options="{
      start: '08:30',
      step: '00:15',
      end: '18:30',
      minTime: startTime
    }">
  </el-time-select>
          </div></el-col>
        </el-row>
      </div></div>

      <sTitle>
      <template #title>固定时间范围</template>
      <template #introduce>若先选择开始时间，则结束时间内备选项的状态会随之改变</template>
    </sTitle>
    <div class="outbox">
      <div class="inbox">
        <el-row type="flex" justify="space-around">
          <el-col :span="10"> <div class="grid-content">
            <el-time-picker
    is-range
    v-model="value4"
    range-separator="至"
    start-placeholder="开始时间"
    end-placeholder="结束时间"
    placeholder="选择时间范围">
  </el-time-picker>
          </div></el-col>
          <el-col :span="10"> <div class="grid-content">
            <el-time-picker
    is-range
    arrow-control
    v-model="value5"
    range-separator="至"
    start-placeholder="开始时间"
    end-placeholder="结束时间"
    placeholder="选择时间范围">
  </el-time-picker>
          </div></el-col>
        </el-row>
      </div></div>
  </div>

</template>

<script>
export default {
  name: 'ColorIndex',
  data () {
    return {
      value1: '',
      value2: new Date(2024, 4, 8, 18, 40),
      value3: new Date(2024, 4, 8, 18, 40),
      startTime: '',
      endTime: '',
      value4: [new Date(2016, 9, 10, 8, 40), new Date(2016, 9, 10, 9, 40)],
      value5: [new Date(2016, 9, 10, 8, 40), new Date(2016, 9, 10, 9, 40)]

    }
  }
}
</script>

<style lang="less" scoped>
@import '../../styles/row.less';
.outbox{
    width: 100%;
    height: auto;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: center;
    .inbox{
      width: 85%;
      height: auto;
      background-color: #ffffff;
      border: 1px solid darkgray;
      .grid-content{
        height: auto;
        background: white;
        margin-bottom: 20px;
        align-items: center;
        }
    }}
</style>
